html {
  overflow: scroll;
}

::-webkit-scrollbar {
    display: none; /* make scrollbar transparent */
}

@-moz-document url-prefix() {
  html,body{overflow: hidden !important;}
}

body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    width: 100vw;
    background-color: rgb(29, 29, 29);
}

/* #root {
  margin: 0;
  padding: 0;
} */

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

/* The way the Slider works, the containing App should have dimensions equal to the dimensions of the slide image, otherwise the image will appear distorted. App is setup so that its dimensions are always 16:9 and only the width parameter is required. It can be made equal to 100vw for responsiveness */
.App {
  --width: 768px;
  width: var(--width);
  height: calc(var(--width) * 9/16);
}
/* Slider */

.slider {
    --white-clr: rgb(233, 233, 233);
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;
    color: var(--white-clr);
}

/* Slide */

.slide {
    position: absolute;
    width: 100%;
    height: 100%;
}

.visible {
    opacity: 1.0;
    transition: opacity 500ms 300ms;
}

.invisible {
    opacity: 0.0;
    transition: opacity 500ms 0ms;
}

/* Slide image */

.bg-img {
    width: 100%;
    height: 100%;
    display: block;
    transition: opacity 0.5s;
}

/* slide text */

.text {
    position: absolute;
    bottom: 25%;
    left: 10%;
    font-size: 300%;
    line-height: 120%;
    margin: 0;
    padding: 0;
    width: 70%;
    font-weight: 500;
    color: var(--white-clr);
}

/* Menu */

.menu {
    position: absolute;
    right: 5%;
    top: 30%;
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: start;
    z-index: 1;
    width: 10%;
}

.menuBar {
    width: 70%;
    margin: 8%;
    padding: 15% 0 15% 0;
    transition: width 0.5s;
}

.menuBar:hover {
    width: 100%;
    cursor: pointer;
}

.extended {
    width: 100%;
}

.menuItem {
    background-color: var(--white-clr);
    height: 3px;
    width: 100%;
}
